.landing-bg{
  width: '100%';
  height: auto;
  position: relative;
  margin:0;
  background: #5f2c82;/* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #49a09d, #5f2c82); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  background-size: 100% 100%;   
  overflow: auto;
}

.clearfix{
  overflow: auto;
}

.header-color{
background: #1D4350;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
background: linear-gradient(to left, #A43931, #1D4350); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.sidenav-color{
  height: 100%;
  width:  100;
  background:#1D4350;  /* fallback for old browsers */
  background:#1D4350;  /* Chrome 10-25, Safari 5.1-6 */
  background:#1D4350; /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  justify-content: space-evenly ;
}

.sidenav-content{
  font-family: 'Quicksand', sans-serif;
  font-weight: 100;
}

/*Landing Page*/

.landing-grid {
 text-align: center;
 top: 0;
 left: 0; 
 width: 100%; 
 height: 100%; 

}

Grid .background {
  background: #0B486B;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #F56217, #0B486B); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  width: 100%; 
  height: 100%; 
  position: absolute;
}
.avatar-img {
  height: 250px; 
  padding-top: 5em;
}

.banner-text {
  background-color: black;
  opacity: .8;
  width: 100%;
  margin : auto;
  border-radius: 10px;
}

.banner-text h1{
  font-size: 66px;
  font-weight: bold;
  color: white;
}

.banner-text hr{
  border-top: 5px dotted white;
  width: 50%;
  margin: auto;
}

.banner-text p {
  color:white; 
  font-size: 20px;
  padding: 1em;
}

.social-links {
  position: relative;
  display: flex;
  justify-content: space-between;
  width: 50%; 
  margin: auto;
}

.social-links i{
  color:white;
  font-size: 4em;
  padding: 8px;
}


/* Projects page */
.projects-grid{
  display:flex;
}


mdl-tabs__tab is-active{
