  height: 100%;
  overflow: auto;
  
}
.contact-grid{
  text-align: center;
  justify-content: center;
  width: 80%;
  height: 500px;
  color: black;
  margin-bottom: 25px;
  padding: 10px
}

.contact-grid h2{
  font-family: 'Quicksand', sans-serif;
  color: black;
}

.contact-grid p{
  font-family: 'oxygen', sans-serif;
  font-weight: bold;
}
.contact-grid hr{
